'use client';
import React from 'react';
import { Box, CssBaseline, AppBar, Toolbar, Typography, Drawer, List, ListItemButton, ListItemText, Button, Alert } from '@mui/material';
import Link from 'next/link';
import { useSession } from '@/features/auth/session';
import { logImpersonationEnd } from '@/services/audit';
import { colors } from '@/theme/colors';
const drawerWidth = 240;

export function AppShell({ children }: { children: React.ReactNode }) {
  const { session, setSession } = useSession();

  const stopImpersonation = React.useCallback(() => {
    if (typeof window === 'undefined') return;
    const baseRaw = window.localStorage.getItem('session_impersonation_base');
    if (session?.impersonatedBy && baseRaw) {
      const base = JSON.parse(baseRaw);
      logImpersonationEnd(base.email, session.email);
      setSession(base);
      window.localStorage.removeItem('session_impersonation_base');
    }
  }, [session, setSession]);

  const navItemSx = {
    width: '60%', // 50% + ~10%
    mx: 'auto',
    color: colors.sidebarText,
    borderRadius: '5px',
    '&:hover': {
      bgcolor: 'rgba(255, 255, 255, 0.04)',
      pr: '10%',
    },
    '&.Mui-selected, &.Mui-selected:hover': {
      bgcolor: colors.navActiveBg,
      color: colors.navActiveText,
      pr: '10%',
    },
    justifyContent: 'flex-start',
  };

  return (
    <Box
      sx={{
        display: 'flex',
        minHeight: '100vh',
        backgroundImage: 'url(/marketing/bg-an.png)',
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover',
        backgroundPosition: 'center',
      }}
    >
      <CssBaseline />
      <AppBar
        position="fixed"
        sx={{
          zIndex: (t) => t.zIndex.drawer + 1,
          bgcolor: 'transparent',
          boxShadow: 'none',
          color: 'inherit',
        }}
      >
        <Toolbar sx={{ justifyContent: 'flex-end' }}>
          {session?.impersonatedBy && (
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
              <Typography variant="body2" sx={{ color: '#212636' }}>
                Impersonating {session.email} as {session.impersonatedBy.email}
              </Typography>
              <Button color="inherit" variant="outlined" onClick={stopImpersonation}>Stop Impersonating</Button>
            </Box>
          )}
        </Toolbar>
      </AppBar>
      <Drawer
        variant="permanent"
        sx={{
          width: drawerWidth,
          '& .MuiDrawer-paper': {
            width: drawerWidth,
            boxSizing: 'border-box',
            bgcolor: colors.sidebarBg,
            color: colors.sidebarText,
          },
        }}
      >
        <Toolbar />
        <Box sx={{ px: 2, pb: 1, display: 'flex', alignItems: 'center', minHeight: 48 }}>
          {session?.agencyLogo ? (
            <img src={session.agencyLogo} alt="Agency Logo" style={{ height: 32, objectFit: 'contain' }} />
          ) : (
            <img src="/marketing/an-logo.png" alt="Athlete Narrative" style={{ height: 32, objectFit: 'contain' }} />
          )}
        </Box>
        <List>
          <ListItemButton LinkComponent={Link} href="/dashboard" sx={navItemSx}>
            <ListItemText primary="Dashboard" />
          </ListItemButton>
          {session?.role === 'parent' && !session?.impersonatedBy && (
            <ListItemButton LinkComponent={Link} href="/agencies" sx={navItemSx}>
              <ListItemText primary="Agencies" />
            </ListItemButton>
          )}
          <ListItemButton LinkComponent={Link} href="/clients" sx={navItemSx}>
            <ListItemText primary="Clients" />
          </ListItemButton>
          <ListItemButton LinkComponent={Link} href="/ai/prompts" sx={navItemSx}>
            <ListItemText primary="AI Prompts" />
          </ListItemButton>
          <ListItemButton LinkComponent={Link} href="/lists" sx={navItemSx}>
            <ListItemText primary="Lists" />
          </ListItemButton>
          <ListItemButton LinkComponent={Link} href="/recruiter" sx={navItemSx}>
            <ListItemText primary="Recruiter" />
          </ListItemButton>
        </List>
      </Drawer>
      <Box
        component="main"
        sx={{
          flexGrow: 1,
          p: 3,
        }}
      >
        <Toolbar />
        {session?.impersonatedBy && (
          <Alert severity="error" sx={{ mb: 2 }}>
            You are impersonating agency account: {session.email}. Actions will be logged.
          </Alert>
        )}
        {children}
      </Box>
    </Box>
  );
}


